var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美膚光療")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("微波熱能除汗系統")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"微波熱能除汗系統","titleEn":"MIRA DRY"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/banner.jpg")))+" alt=\"微波熱能除汗系統 Banner\"></div> <div class=\"article-container\"><h2>美麗功效</h2> <p>每個人都會流汗，尤其當天氣炙熱，腋下出汗總是特別旺盛，導致衣服留下明顯的汗漬，同時腋下出汗過多也會產生難聞的異味，使用除臭劑或者止汗劑又擔心會毀了心愛的衣物。在很多場合，腋下出汗總會造成很多尷尬，同時出汗過多也會產生難聞的異味，嚴重拉遠了人與人之間的距離，所以千萬不要讓汗水破壞了專業上的形象或是親密的時刻！！</p> <p>微波熱能除汗系統是合法無傷口、無修復期可以根治腋下出汗、腋下多汗症止汗、腋臭的方法，同時通過美國FDA核准的無創腋下出汗止汗儀器，能根除了腋臭異味體臭的禍源。</p></div> <div class=\"article-container\"><h2>微波熱能除汗系統(MiraDry) 原理</h2> <p>微波熱能除汗系統能量為微波熱能量，到達此微波溫度才能將汗腺萎縮，微波熱能除汗系統會利用冷卻系統，來保護表皮及真皮上部等其他組織不會產生灼傷。已經被破壞的汗腺不會新生，對於治療腋下多汗症可以達到持續性的效果。</p> <h3>大汗腺與汗腺解剖圖</h3> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/1.jpg")))+" alt=\"大汗腺與汗腺解剖圖\"> <h3>微波熱能除汗系統作用原理</h3> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/2.jpg")))+" alt=\"微波熱能除汗系統作用原理\"> <h5>✔ 能量精準傳遞</h5> <p>波頻率5.8GHz能精準地將能量傳遞至真皮層與脂肪交界處(大小汗腺分佈的區域)有效破壞大汗腺(頂漿腺-異味來源)及小汗腺(多汗來源)。</p> <h5>✔ 關鍵治療溫度</h5> <p>治療能量溫度維持60°C，有效讓汗腺萎縮不再新生，維持持續性效果15°C冷卻系統維持20秒，保護表皮及上真皮層不受熱傷害。</p></div> <div class=\"article-container\"><h2>適用對象</h2> <h3>多汗症自我檢視表</h3> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/3.png")))+" alt=\"多汗症自我檢視表\"> <h3>狐臭嚴重程度辨別</h3> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/4.png")))+" alt=\"狐臭嚴重程度辨別表\"></div> <div class=\"article-container\"><h2>治療前準備事項</h2> <p><b>治療前4-6天</b>於兩邊腋下進行除包，等到治療當日，腋下會長出少量毛髮，以便確定治療區域。</p> <h5>治療當日建議穿著:袖口寬鬆、易清潔的上衣</h5> <p>女仕 - 背心、運動型胸衣及背心</p> <p>男仕 - 背心，或者不穿上衣</p></div> <div class=\"article-container\"><h2>通常需要幾次療程</h2> <ul><li>- 雙側腋下可能效果不同</li> <li>- 大部分病患接受一次或一次以上的治療</li> <li>- 若需第二次治療，建議間隔三個月以上</li> <li>- 治療當天不可搭配其他腋下刺激性療程，如:腋下除毛雷射、腋下美白雷射..等</li> <li>- 建議術後三個月，由醫師評估狀況</li></ul></div> <div class=\"article-container\"><h2>治療步驟</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/5.png")))+" alt=\"治療步驟\"></div> <div class=\"article-container\"><h2>美麗功效</h2> <ul><li>✦ 一次治療、三種效果:汗、臭、毛</li> <li>✦ 非侵入式治療，無疤痕，安全性高</li> <li>✦ 術後恢復期短，無代償性出汗</li> <li>✦ 長久且穩定的治療效果</li> <li>✦ 研究顯示可降低平均82%腋下排汗量</li> <li>✦ 治療後的患者滿意度高達92%以上</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <img"+(_vm._ssrAttr("src",require("assets/img/service/preventiveMedicine/miraDry/6.png")))+" alt=\"術後小叮嚀\"> <ul><li>✦ 術後常見反應，狀況依個人體質而定</li> <li>✦ 術後冰敷可減輕不適感及腫脹感，冰敷注意事項：</li> <li>-- 術後前48小時，每2-4小時冰敷治療部位，每次10-15分鐘</li> <li>-- 一般術後立即用75%酒精清潔後，將冰袋以無菌紗布包覆後再冰敷，以避免凍傷</li> <li>✦ 治療區域須保持清潔(溫和的清潔)</li> <li>✦ 治療後一週內避免做劇烈運動及使用止汗劑/體香劑</li> <li>✦ 治療後兩週內腋下部位會較敏感，此時避免進行腋下除貓毛</li> <li>✦ 視情況依醫囑給予口服止痛藥</li> <li><h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></li></ul></div> <div class=\"article-container\"><h2>醫生小叮嚀</h2> <h3>微波熱能</h3> <p>改善體味是因為其止汗效果，若是重度異味患者則需要多次治療。狐臭患者要去除身上的特殊異味，需要清除或破壞腋下的頂漿腺才能達到100%滿意度。建議狐臭患者可加上微創狐臭手術，擺脫意味的折磨。</p> <h3>微創狐臭手術</h3> <p>由醫生手工將頂漿腺慢慢剪除，不像傳統手術有疤痕過大的問題，也可以達到較佳的廓清率，因此手術後滿意度相當高，但術後修復期也較長。</p></div>")],2)}
